import React,  { useState } from 'react';
import QrReader from 'react-web-qr-reader';
import Switch from '@material-ui/core/Switch';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Button from '@material-ui/core/Button';
import {Link} from 'react-router-dom';
import users from '../../userData';
import './index.css';



function Camera() {
  const delay = 500;
  const [cameraType,setCameraType] = useState(false);

  const [result, setResult] = useState(null);

  const handleScan = (resul) => {
    console.log(resul)
    if (resul) {
      const user = users.filter(item=>item.token===resul.data)[0] ||null;
      setResult(user);
      if(user == null) alert('QRCODE INVÁLIDO!')
    }
  };

  const handleError = (error) => {
    console.log(error);
  };
  const previewStyle = {
    height: 240,
    width: 320,
  };

  const handleChange = (event) => {
    setCameraType(event.target.checked);
  };
  return (
    <div className="Container">
      
      {
        result==null ? 
        (
          <>
          <div className="containerCamera">

            <div className="Camera">
              <QrReader 
                facingMode={cameraType?'user':'environment'}
                style={previewStyle}
                delay={delay}
                onError={handleError}
                onScan={handleScan}
                />
            </div>
                <Switch
                  checked={cameraType}
                  onClick={handleChange}
                  name="CameraType"
                  inputProps={{ 'aria-label': 'switch camera type' }}
                  />

              <Link to= "/">HOME</Link>
          </div>
          </>
        ) : (
          <>
            <Card className='card'>
              <CardContent>
                <img src={result.imageUrl} alt='user'/>
                <p>Nome: {result.name}</p>
                <p>Token: {result.token}</p>
                {result.auth && 
                
                <p className="allow">AUTORIZADO</p>
                }
                
                {!result.auth && 
                  <div className='notAllow'>NÃO AUTORIZADO!!!</div>
                }
              </CardContent>
              <CardActions>
                <Button size="small" onClick={()=>setResult(null)}>OK</Button>
              </CardActions>
            </Card>
          </>
        )}

    </div>
  
  );
};


export default Camera;
